import { GetToken } from "@/utilities/cookies.js";
import ProductCompare from "../../Components/ProductCompare/index.jsx";
import createMetadata from "@/Components/Seo/createMetadata.jsx";

export async function generateMetadata() {
  const title = "Compare Product";
  return createMetadata({
    title: title,
  });
}

export default async function page() {
  const token = await GetToken();

  return <ProductCompare token={token} />;
}
